














































































































































import { Vue, Component, Prop } from 'vue-property-decorator'
@Component({})
export default class ItemsEdit extends Vue {
  @Prop({ type: String }) id

  declare model
  declare categories: []
  declare items: []

  data () {
    return {
      activeName: 'basic',
      categories: [], // 类型
      items: [], // 物品
      model: {
        avatar: '', // 头像
        scores: {
          difficult: 0, // 难度
          skills: 0, // 技能
          attack: 0, // 攻击
          survive: 0 // 生存
        },
        skills: []
      }
    }
  }

  afterUpload (res) {
    this.model.avatar = res.url
  }

  async save () {
    if (this.id) {
      await this.$http.put(`rest/heros/${this.id}`, this.model)
    } else {
      await this.$http.post('rest/heros', this.model)
    }
    this.$message({
      type: 'success',
      message: '保存成功'
    })
    this.$router.push('/heros/list')
  }

  async fetch () {
    const result = await this.$http.get(`/rest/heros/${this.id}`)
    this.model = result.data
  }

  async fetchCategories () {
    const result = await this.$http.get('/rest/categories')
    this.categories = result.data
  }

  async fetchItems () {
    const result = await this.$http.get('/rest/items')
    this.items = result.data
  }

  created () {
    this.fetchItems()
    this.fetchCategories()
    this.id && this.fetch()
  }
}
